.login-button {
  display: flex;
  justify-content: flex-end;
  margin: 20px;
}


.nav-section {
  display: flex;
  justify-content: center;
}
.hnm-logo {
  width: 100px;
  height: 80px;
}

.menu-area {
  display: flex;
  justify-content: center;
}

.menu-list {
  display: flex;
  list-style: none;
}

.menu-list li {
  padding: 10px;
}


/* 버튼 css https://codepen.io/huange/pen/bGqMqM */
.search {
  width: 100%;
  position: relative;
  display: flex;
  margin-top: 18px;
  margin-left: 30px;
}

.searchTerm {
  width: 100%;
  border: 3px solid #00B4CC;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9DBFAF;
}

.searchTerm:focus{
  color: #00B4CC;
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid #00B4CC;
  background: #00B4CC;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}